module $ { export class $mol_stringer_demo extends $mol_rower {

	/// name \
	@ $mol_prop()
	name( ...diff : any[] ) {
		return ( diff[0] !== void 0 ) ? diff[0] : ""
	}

	/// one $mol_stringer value > name
	@ $mol_prop()
	one( ...diff : any[] ) {
		return ( diff[0] !== void 0 ) ? diff[0] : new $mol_stringer().setup( __ => { 
			__.value = ( ...diff : any[] ) => this.name(  ...diff )
		} )
	}

	/// two $mol_stringer 
	/// 	value > name 
	/// 	hint \Batman
	@ $mol_prop()
	two( ...diff : any[] ) {
		return ( diff[0] !== void 0 ) ? diff[0] : new $mol_stringer().setup( __ => { 
			__.value = ( ...diff : any[] ) => this.name(  ...diff )
			__.hint = () => "Batman"
		} )
	}

	/// three $mol_stringer 
	/// 	value > name 
	/// 	hint \Jocker
	/// 	disabled : true
	@ $mol_prop()
	three( ...diff : any[] ) {
		return ( diff[0] !== void 0 ) ? diff[0] : new $mol_stringer().setup( __ => { 
			__.value = ( ...diff : any[] ) => this.name(  ...diff )
			__.hint = () => "Jocker"
			__.disabled = () => ( <any> true )
		} )
	}

	/// childs / 
	/// 	< one 
	/// 	< two 
	/// 	< three
	childs() {
		return [].concat( this.one() , this.two() , this.three() )
	}

} }

