module $ { export class $mol_switcher_demo extends $mol_switcher {

	/// color \
	@ $mol_prop()
	color( ...diff : any[] ) {
		return ( diff[0] !== void 0 ) ? diff[0] : ""
	}

	/// value > color
	value( ...diff : any[] ) {
		return this.color(  ...diff )
	}

	/// options * 
	/// 	red \Red
	/// 	green \Green
	/// 	blue \Blue
	options() {
		return $mol_merge_dict( super.options() , {
			"red" : ()=> <any> "Red" ,
			"green" : ()=> <any> "Green" ,
			"blue" : ()=> <any> "Blue" ,
		} )
	}

} }

