module $ { export class $mol_demo_all extends $mol_viewer {

	/// name \$mol_viewer
	name() {
		return "$mol_viewer"
	}

	/// medium $mol_demo_medium 
	/// 	name < name 
	/// 	title \Fit to content
	@ $mol_prop()
	medium( ...diff : any[] ) {
		return ( diff[0] !== void 0 ) ? diff[0] : new $mol_demo_medium().setup( __ => { 
			__.name = () => this.name()
			__.title = () => "Fit to content"
		} )
	}

	/// small $mol_demo_small 
	/// 	name < name 
	/// 	title \Minimum screen
	@ $mol_prop()
	small( ...diff : any[] ) {
		return ( diff[0] !== void 0 ) ? diff[0] : new $mol_demo_small().setup( __ => { 
			__.name = () => this.name()
			__.title = () => "Minimum screen"
		} )
	}

	/// large $mol_demo_large 
	/// 	name < name 
	/// 	title \Maximize to screen
	@ $mol_prop()
	large( ...diff : any[] ) {
		return ( diff[0] !== void 0 ) ? diff[0] : new $mol_demo_large().setup( __ => { 
			__.name = () => this.name()
			__.title = () => "Maximize to screen"
		} )
	}

	/// childs / 
	/// 	< medium 
	/// 	< small 
	/// 	< large
	childs() {
		return [].concat( this.medium() , this.small() , this.large() )
	}

} }

